label {
  font-family: "Montserrat", sans-serif;
}

textarea,
input,
input[type="date"] {
  font-family: "Open Sans", sans-serif;
}

input {
  padding: 20px;
}

.modal-description-input {
  height: 100px !important;
}

.modal-bg {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal {
  background: white;
  color: white;
  z-index: 10;
  height: 400px;
  min-width: 300px;
  border-radius: 16px;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
}

.modal-header {
  overflow: hidden;
  height: 50px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #9c556d;
  color: white;
}

.modal-heading {
  margin: 0;
  padding: 10px;
  color: white;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  font-family: "Monterrat", sans-serif;
}

.modal-close {
  font-size: 20px;
  margin-right: 10px;
  outline: none;
  border: none;
  color: white;
  background: none;
}

.modal-content {
  padding: 10px;
  font-size: 14px;
  color: black;
  display: flex;
  flex-direction: column;
  overflow-y: auto !important;
  overflow-x: hidden;
}

label {
  font-weight: 600;
  margin-bottom: 5px;
  margin-top: 10px;
}

input,
textarea {
  padding: 5px;
}

.modal-footer {
  margin-bottom: 10px;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.modal-actions {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-full button {
  border: 0;
  outline: 0;
  margin: 15px;
  padding: 10px;
}

.modal-full button:hover {
  cursor: pointer;
}

.modal-full {
  display: none;
}

.modal-confirm,
.modal-cancel {
  border: 1px solid black;
  padding: 0 10px;
  font-size: 14px;
  width: 80px;
  transition: 0.5s ease;
  background-color: #9c556d;
  color: white;
  border-radius: 5px;
  font-family: "Open Sans", sans-serif;
}

.modal-confirm:hover,
.modal-cancel:hover {
  background-color: #bd7990;
}

.modal-title-input {
  font-family: "Open Sans", sans-serif;
}

@media (min-width: 1000px) {
  .modal {
    height: auto;
    width: 45vw;
  }

  .modal-description-input {
    padding-left: 20px;
    min-height: 10vw !important;
  }
  input[type="date"] {
    padding: 0.9vw !important;
  }

  .modal-confirm,
  .modal-cancel {
    font-size: 1.3vw;
    padding: 0.7vw !important;
  }

  .modal-header {
    height: min-content;
  }

  .modal-heading {
    font-size: 30px;
  }

  .project-title {
    font-size: 2vw !important;
  }

  .modal-confirm,
  .modal-cancel {
    width: 30%;
  }

  .modal-footer {
    padding: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .modal-content {
    height: auto;
  }
}
