* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  font-family: "Open Sans", sans-serif;
  height: 100%;
  font-size: 16px;
  background-color: #fdfdfd;
}

body {
  min-height: 100vh;
}

svg,
i {
  pointer-events: none;
}

li {
  list-style-type: none;
}

button:hover {
  cursor: pointer;
}

.project {
  padding-bottom: 50px;
}

#react-burger-menu-btn {
  position: absolute;
  top: 10px;
  width: 20px;
  height: 20px;
}

#root {
  height: 100%;
}

.App {
  height: 100%;
}

.task {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.5s ease;
  padding: 15px;
  border-bottom: 1px solid rgb(233, 233, 233);
}

.task:hover {
  background-color: rgb(246, 246, 246);
}

.task-left {
  display: flex;
  align-items: center;
  width: 100%;
}

.task-completed {
  background: none;
  border: 0;
  outline: 0;
  margin-top: 5px;
  color: #9c556d;
  font-size: 16px;
}

.task-details {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.bottom-details {
  font-size: 14px;
  color: grey;
}

.divider {
  margin-left: 5px;
  margin-right: 5px;
}

.task-right {
  margin-right: 10px;
  margin-left: 10px;
  position: relative;
  display: flex;
}

.task-right button {
  background: none;
  border: 0;
  font-size: 16px;
  margin-left: 5px;
}

.importantOn {
  font-size: 16px;
  color: gold;
  margin-top: 5px;
}

.importantOff {
  font-size: 16px;
  margin-top: 5px;
  color: black;
}

.edit-task:hover,
.delete-task:hover {
  cursor: pointer;
}

.tasklist {
  padding-top: 70px;
  overflow-y: auto;
}

.tasklist h3 {
  border-bottom: 1px solid black;
  width: 100%;
  padding-bottom: 15px;
}

.tasklist,
.completed-box {
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  padding-bottom: 30px;
}

.edit-project,
.project-input,
.task-title-edit {
  display: none;
}

.completed-title:hover {
  cursor: pointer;
}

.completed .task-title {
  text-decoration: line-through;
}

.completed {
  padding-bottom: 30px;
}

.task-title-edit {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  border: 0;
  outline: 0;
  padding: 0;
  width: 100%;
  height: auto;
  background: none;
}

.task-input {
  position: fixed;
  bottom: 0;
  z-index: 1;
  width: 100%;
}

.input-task {
  width: 100%;
  position: relative;
  font-family: "Open Sans", sans-serif;
  border: 1px solid black;
  padding: 15px;
  padding-right: 70px;
  background-color: #fdfdfd;
  border-radius: 0;
  outline: 0;
  font-size: 16px;
}

.input-task:disabled {
  background-color: #fafafa;
}

.input-task:focus,
.input-task:active {
  border: 1px solid black;
}

.add-task {
  position: absolute;
  right: 0px;
  height: 100%;
  padding: 20px;
  background-color: #9c556d;
  color: white;
  border: 1px solid black;
  outline: 0;
  font-size: 16px;
  transition: 0.5s ease;
}

.add-task:hover {
  background-color: #bd7990;
}

.project-input {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  background: none;
  padding: 10px 5px;
  width: 95%;
}

.project-nav-box {
  padding-bottom: 60px;
}

.project-nav button {
  margin-left: 10px;
  display: none;
}

.project-delete {
  background: 0;
  outline: 0;
  border: 0;
  transition: 0.5s ease;
  font-size: 16px;
}

.project-delete:hover {
  cursor: pointer;
  color: #9c556d;
}

.project-nav-item:hover > .project-delete {
  display: inline-block;
  transition: 0.5s ease;
}

.top-nav-item,
.project-nav-item {
  padding: 10px 5px;
  transition: 0.5s ease;
  border-bottom: 1px solid black;
}

.bm-menu {
  height: 100vh;
  min-width: 300px;
  width: 20vw;
  z-index: 1000;
  background-color: #ffffff;
  overflow-y: auto !important;
}

.bm-icon {
  color: white;
}

.bm-menu-wrap {
  top: 0;
}

.bm-item-list {
  height: 90% !important;
  width: 300px;
}

.bm-menu h2 {
  margin-right: 35px;
  margin-top: 10px;
  margin-left: 15px;
}

.bm-menu li {
  transition: 0.5s ease;
}

.bm-menu li:hover {
  color: #9c556d;
  cursor: pointer;
}

.navlists {
  max-height: 100%;
}

.open-nav {
  font-size: 20px;
  top: 10px;
  left: 15px;
  height: 20px;
  width: 20px;
  color: black;
  position: fixed;
}

.close-nav,
.open-nav {
  background: none;
  border: 0;
  outline: 0;
}

.open-nav:hover {
  cursor: pointer;
}

.completed-title {
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 1px solid black;
  padding-bottom: 15px;
}

.completed-closed {
  display: none;
}

.completed-title h3 {
  margin-left: 10px;
}

.close-nav {
  position: absolute;
  right: 13px !important;
  font-size: 20px;
  top: 13px !important;
  z-index: 0;
}

.close-nav:hover {
  cursor: pointer;
}

.top-nav-box {
  padding-right: 15px;
}

.top-nav-box h3 {
  margin-right: 0;
}

.project-nav {
  padding-right: 15px;
}
.project-title-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #9c556d;
  color: white;
  width: 100%;
  margin-bottom: 20px;
  z-index: 1;
  padding: 10px 0;
}

.help {
  margin-right: 30px;
  font-size: 20px;
  background: none;
  border: 0;
  color: white;
}

.add-project {
  width: inherit;
  padding: 15px;
  bottom: 0;
  left: 0;
  position: fixed;
  font-family: "Open Sans", sans-serif;
  background-color: #9c556d;
  outline: 0;
  border: 0;
  transition: 0.5s ease;
  color: white;
  font-size: 16px;
}

.add-project:hover {
  background-color: #bd7990;
}

.nav-background {
  width: 100vw;
  height: 100vh;
  background-color: #000000a8;
  z-index: 1;
  position: absolute;
}

.project-nav li {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.project-nav-title svg {
  margin-right: 5px;
}

.project-nav-title {
  width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}

.top-nav-box,
.project-nav {
  margin-left: 15px;
}

.bm-overlay {
  top: 0;
}

.bm-menu input {
  border: 0;
  outline: 0;
  border-bottom: 1px solid black;
}

.bm-menu h3 {
  margin-top: 30px;
  border-bottom: 1px solid black;
  padding-bottom: 10px;
}

.project-title,
.edit-project {
  width: 100%;
  font-size: 24px;
  padding-bottom: 5px;
  margin-left: 50px;
  font-family: "Montserrat", sans-serif;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  font-weight: 500;
}

.edit-project {
  padding: 0;
  margin: 0;
  margin-left: 50px;
  padding-bottom: 5px;
  font-size: 24px;
  width: 85%;
  outline: 0;
  border: 0;
  background-color: #9c556d;
  color: white;
}

.task-title {
  display: block;
  width: 100%;
}

/* iPads and Tablets */
@media (min-width: 1000px) {
  .task {
    padding: 1em;
  }

  .bm-item-list {
    width: 100%;
    padding-right: 10px;
  }

  .bm-menu-wrap {
    width: auto;
  }

  .add-project {
    width: 300px;
  }
  .project-title {
    font-size: 2.5vw;
    margin-top: 0.3vw;
    margin-left: 3.9vw;
  }

  .open-nav {
    top: 1.2vw;
    left: 15px;
    min-height: 18px;
    min-width: 18px;
    height: 1.5vw;
    width: 1.5vw;
  }
}

@media (min-width: 1700px) {
  *,
  .input-task {
    font-size: 1vw;
  }
  .tasklist h3 {
    margin-top: 20px;
  }
  .bm-menu .app-title {
    font-size: 1.5vw;
  }

  .bm-menu-wrap {
    width: auto;
  }
  .close-nav,
  .bm-cross-button {
    width: 40px !important;
    height: 40px !important;
  }

  .add-project {
    font-size: 1vw;
    width: 20vw;
  }

  .project-title {
    font-size: 2.5vw;
    margin-top: 0.3vw;
    margin-left: 3.5vw;
  }

  .open-nav {
    top: 1vw;
    left: 20px;
    height: 1.5vw;
    width: 1.5vw;
  }

  .project-title-box {
    height: 3.5vw;
  }
}
