.tutorial-btns {
  display: flex;
  padding: 5px;
  padding-left: 0;
  align-items: flex-end;
  font-size: 12px;
  margin-top: 20px;
}

.skip {
  text-decoration: underline;
  transition: 0.5s ease;
  margin-left: 5px;
}

.skip:hover {
  cursor: pointer;
  text-decoration: none;
  color: #00f5b8;
}

.tutorial-preview {
  position: fixed;
  display: none;
  right: 50px;
  width: 200px;
  top: 60px;
  background-color: #3498db;
  padding: 15px;
}

.help:hover > .tutorial-preview {
  position: fixed;
  display: flex;
  transition: 0.5s ease;
}

.next {
  border: 0;
  outline: 0;
  background: none;
  color: white;
  text-align: center;
  border: 1px solid white;
  padding: 3px;
  margin-top: 10px;
  width: 50px;
  transition: 0.5s ease;
}

.next:hover {
  background: white;
  color: #3498db;
}

@media (min-width: 0px) and (max-width: 480px) {
  .box,
  .box-regular {
    width: 250px !important;
  }

  .box-7 {
    position: fixed;
    display: none;
    left: 30px;
    top: 10px;
  }

  .box-8 {
    width: 225px !important;
  }

  .box-10 {
    width: 300px !important;
    font-size: 12px;
  }

  .box-13 {
    width: 310px !important;
    font-size: 13px;
  }
  .box-15 {
    position: fixed;
    display: none;
    left: 30px;
    top: 50px;
  }
}
